@import url('https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@500;700;900&display=swap');
:root {
    --color-very-plain-blue: hsl(225, 100%, 98%);
    --color-desaturated-blue: hsl(224, 23%, 55%);
    --color-birght-blue: hsl(245, 75%, 52%);  
    --color-pale-blue: hsl(225, 100%, 94%);
    --color-dark-blue: hsl(223, 47%, 23%);
    --color-white: #fff;
    --box-shadow: 0px 10px 33px -17px;
    --xs: 0.675rem;
    --sm: 1.3rem;
    --m: 1.8rem;
    --l: 2.8rem;
    --xl: 3.8rem;
}
* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-family: 'Red Hat Display', sans-serif;
}

// global css
img {
    width: 100%;
    height: 100%;
}

html {
    font-size: 15px;
}

body {
    min-height: 100vh;
    max-width: 1400px;
    width: 90%;
    margin: 0 auto;

    main {
        -webkit-box-shadow: 0px 5px 36px -16px rgba(31,47,86,1);
                box-shadow: 0px 5px 36px -16px rgba(31,47,86,1);
        background-color: hsl(225, 100%, 94%);
        background-color: var(--color-pale-blue);
        position: relative;
        min-height: 100vh;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        picture {
            position: absolute;
            z-index: 1;
            img {
                width: 100%;
            }
        }

        .summery-wall {
            display: block;
            position: absolute;
            top: 0; left: 0;
        }

        .container-section {
            -webkit-box-shadow: 0px 5px 36px -16px rgba(31,47,86,1);
                    box-shadow: 0px 5px 36px -16px rgba(31,47,86,1);
            display: grid;
            grid-template-rows: 1fr;
            max-width: 23.5rem;
            margin: 1.8rem;
            margin: var(--m);
            text-align: center;
            background-color: #fff;
            background-color: var(--color-white);
            border-radius: 1.3rem;
            border-radius: var(--sm);
            z-index: 2;
        }

        .summery-hero {
            img {
                border-top-right-radius: 1.3rem;
                border-top-right-radius: var(--sm);
                border-top-left-radius: 1.3rem;
                border-top-left-radius: var(--sm);
            }
        }

        .summery-details {
            padding-left: 2.8rem;
            padding-right: 2.8rem;
            padding-left: var(--l);
            padding-right: var(--l);
            padding-top: 1.8rem;
            padding-top: var(--m);
            padding-bottom: 1.3rem;
            padding-bottom: var(--sm);
        }

        .summery-description {
            h1 {
                font-weight: 900;
                color: hsl(223, 47%, 23%);
                color: var(--color-dark-blue);
                font-size: 1.3rem;
                font-size: var(--sm);
                margin-bottom: 0.675rem;
                margin-bottom: var(--xs);
            }

            p {
                color: hsl(224, 23%, 55%);
                color: var(--color-desaturated-blue);
                line-height: 1.4;
                font-size: 0.950rem;
            }
        }

        .summery-subscribtion-section {
            display: grid;
            grid-template-columns: 1.3fr 2fr 2fr;
            background-color: hsl(225, 100%, 98%);
            background-color: var(--color-very-plain-blue);
            -webkit-box-pack: center;
                -ms-flex-pack: center;
                    justify-content: center;
            -webkit-box-align: center;
                -ms-flex-align: center;
                    align-items: center;
            padding: 0.675rem;
            padding: var(--xs);
            margin-top: 1.3rem;
            margin-bottom: 1.3rem;
            margin-top: var(--sm);
            margin-bottom: var(--sm);
            border-radius: 0.675rem;
            border-radius: var(--xs);
        }

        .icon {
            width: 2.8rem;
            width: var(--l);
            margin-right: 0.675rem;
            margin-right: var(--xs);
        }

        .summery-plan {
            text-align: left;
            span:nth-of-type(1) {
                color: hsl(223, 47%, 23%);
                color: var(--color-dark-blue);
                font-weight: 900;
            }

            span:nth-of-type(2) {
                color: hsl(224, 23%, 55%);
                color: var(--color-desaturated-blue);
            }
        }

        .link {
            text-align: right;

            a:hover {
                color: hsl(224, 23%, 55%);
                color: var(--color-desaturated-blue);
                -webkit-text-decoration: none;
                text-decoration: none;
            }
        }

        .summery-checkout {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
                -ms-flex-direction: column;
                    flex-direction: column;
            margin-bottom: 1.3rem;
            margin-bottom: var(--sm);

            button {
                padding-top: 0.675rem;
                padding-bottom: 0.675rem;
                padding-top: var(--xs);
                padding-bottom: var(--xs);
                background-color: hsl(245, 75%, 52%);
                background-color: var(--color-birght-blue);
                color: #fff;
                color: var(--color-white);
                border-radius: 0.675rem;
                border-radius: var(--xs);
                -webkit-transition: background-color 250ms ease-in-out;
                transition: background-color 250ms ease-in-out;
                cursor:pointer;
                border: none;
                margin-bottom: 1.8rem;
                margin-bottom: var(--m);
                -webkit-box-shadow: 0px 5px 36px -16px rgba(56,41,224,1);
                        box-shadow: 0px 5px 36px -16px rgba(56,41,224,1);
            }

            button:hover {
                background-color:hsl(245, 76%, 60%);
            }

            a {
                color: hsl(224, 23%, 55%);
                color: var(--color-desaturated-blue);
                -webkit-text-decoration: none;
                text-decoration: none;
                font-weight: 700;
            }

            a:hover {
                color: hsl(223, 47%, 23%);
                color: var(--color-dark-blue);
            }
        }
    }

    footer {
        background-color: hsl(225, 100%, 94%);
        background-color: var(--color-pale-blue);
        padding: 2.8rem;
        padding: var(--l);
        margin-top: 3.8rem;
        margin-top: var(--xl);
        margin-bottom: 0.2rem;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        color: hsl(223, 47%, 23%);
        color: var(--color-dark-blue);
        
        a {
            padding-left: 1.3rem;
            padding-right: 1.3rem;
            padding-left: var(--sm);
            padding-right: var(--sm);
            color: hsl(223, 47%, 23%);
            color: var(--color-dark-blue);
            -webkit-text-decoration: none;
            text-decoration: none;
            font-weight: 900;
        }

        a:hover {
            color: hsl(224, 23%, 55%);
            color: var(--color-desaturated-blue);
        }
    }
}